import { ImageResponse } from "next/og";

export const size = {
  width: 1200,
  height: 630,
};

export const contentType = "image/png";

export default function OgImage() {
  return new ImageResponse(
    (
      <div
        style={{
          width: "100%",
          height: "100%",
          background: "linear-gradient(135deg, #3b82f6 0%, #2563eb 100%)",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
        }}
      >
        <div
          style={{
            width: 300,
            height: 300,
            borderRadius: 72,
            backgroundColor: "white",
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            boxShadow: "0 30px 120px rgba(15, 23, 42, 0.35)",
          }}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 512 512"
            width={220}
            height={220}
          >
            <defs>
              <linearGradient id="ogGradient" x1="0" x2="1" y1="0" y2="1">
                <stop offset="0" stopColor="#0ea5e9" />
                <stop offset="1" stopColor="#2563eb" />
              </linearGradient>
            </defs>
            <rect x="80" y="64" width="352" height="352" rx="96" fill="url(#ogGradient)" />
            <path
              d="M256 176h48v112h112v48H304v112h-48V336H144v-48h112z"
              fill="#ffffff"
            />
          </svg>
        </div>
      </div>
    ),
    size
  );
}
